.message-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.message {
  position: relative;
  margin-bottom: 12px;
  padding: 4px 15px;
  background-color: #89b6f7;
  border-radius: 9999px;
}

.message-wrapper--mine .message {
  padding: 4px 16px;
}

.text {
  display: inline-block;
  font-size: 21px;
  margin: 5px 0;
}

.time {
  color: #676767;
}



